body .card-item.ant-card {
  background: #FFFFFF;
  border: 1px solid #EAEAEA;
  word-break: break-word;
  min-height: 100%;
  display: flex;
  flex-direction: column;
  font-weight: 600;
  font-size: 11px;
  color: #989CA8;
  letter-spacing: 0;
  text-align: left;
}
body .card-item.ant-card .card-children {
  width: 100%;
  height: 100%;
  flex: 1 1;
  display: flex;
  flex-direction: column;
}
body .card-item.ant-card.ant-card.center .card-children {
  justify-content: center;
  align-items: center;
  text-align: center;
}
body .card-item.ant-card.condensed-card .card-children {
  flex-direction: row;
}
body .card-item.ant-card.condensed-card .ant-card-body {
  padding: 13px;
  flex-direction: row;
}
body .card-item.ant-card.condensed-card .condensed-card-left {
  display: flex;
  flex: 1 1;
  flex-direction: column;
  align-items: flex-start;
  align-self: flex-end;
}
body .card-item.ant-card.condensed-card .condensed-card-left .condensed-card-title {
  font-weight: 700;
  font-size: 40px;
  line-height: 45px;
  color: #0D1B2A;
  letter-spacing: 0;
}
body .card-item.ant-card.condensed-card .condensed-card-left .condensed-card-description {
  font-weight: 700;
  font-size: 11px;
  line-height: 11px;
  color: #9B9B9B;
  letter-spacing: 0;
  text-transform: uppercase;
}
body .card-item.ant-card.condensed-card .condensed-card-right {
  display: flex;
  width: 63px;
  height: 63px;
  background: #0053FA;
  justify-content: center;
  align-items: center;
}
body .card-item.ant-card.condensed-card .condensed-card-right.red {
  background: #FF5F5F;
}
body .card-item.ant-card.condensed-card .condensed-card-right.green {
  background: #5DBE42;
}
body .card-item.ant-card.condensed-card .condensed-card-right svg {
  min-width: 33px;
  min-height: 33px;
  color: white;
}
body .card-item.ant-card .card-edit {
  display: none;
  width: 25px;
  height: 25px;
  padding: 5px;
  top: 28px;
  left: 75%;
  -webkit-transform: translate(-50%, 0);
          transform: translate(-50%, 0);
  position: absolute;
  cursor: pointer;
}
body .card-item.ant-card .card-edit svg {
  color: #B5B9C5;
}
body .card-item.ant-card .card-close {
  width: 25px;
  height: 25px;
  padding: 5px;
  top: 21px;
  right: 10px;
  text-align: right;
  -webkit-transform: translate(-50%, 0);
          transform: translate(-50%, 0);
  position: absolute;
  cursor: pointer;
}
body .card-item.ant-card .card-close svg {
  color: #B5B9C5;
}
body .card-item.ant-card .card-remove {
  display: none;
  width: 25px;
  height: 25px;
  padding: 5px;
  position: absolute;
  top: 28px;
  left: 25%;
  -webkit-transform: translate(-50%, 0);
          transform: translate(-50%, 0);
  cursor: pointer;
}
body .card-item.ant-card .card-remove svg {
  color: #FF5F5F;
}
body .card-item.ant-card:hover .card-remove,
body .card-item.ant-card:hover .card-edit {
  display: block;
}
body .card-item.ant-card.shadow {
  box-shadow: 0 10px 50px 0 rgba(0, 116, 249, 0.1);
}
body .card-item.ant-card.primary {
  background: rgba(0, 116, 249, 0.03);
  border: 1px solid rgba(0, 116, 249, 0.1);
  box-shadow: 0 1px 10px 0 rgba(0, 116, 249, 0.1);
}
body .card-item.ant-card.primary .card-close svg {
  color: #0053FA;
}
body .card-item.ant-card .ant-card-body {
  display: flex;
  flex-direction: column;
  align-items: center;
  position: relative;
  flex: 1 1;
}
body .card-item.ant-card .ant-avatar {
  background: transparent;
}
body .card-item.ant-card .card-avatar.ant-avatar {
  border-radius: 14px;
  color: white;
  margin-bottom: 10px;
  background-color: #0053FA;
}
body .card-item.ant-card .card-avatar.ant-avatar .ant-avatar-string {
  -webkit-transform: translateX(-50%) !important;
          transform: translateX(-50%) !important;
  text-transform: uppercase;
  font-weight: 700;
  font-size: 10px;
  color: #FFFFFF;
  letter-spacing: 2px;
  background: transparent;
}
body .card-item.ant-card .card-item .space {
  margin-top: 10px;
}
body .card-item.ant-card .card-title {
  width: 100%;
  font-weight: 600;
  font-size: 16px;
  color: #0D1B2A;
  letter-spacing: 0;
  text-align: center;
  line-height: 19px;
  margin-bottom: 24px;
}
body .card-item.ant-card .card-title.left {
  width: 100%;
  -moz-text-align-last: left;
       text-align-last: left;
}
body .card-item.ant-card .card-title.right {
  width: 100%;
  -moz-text-align-last: left;
       text-align-last: left;
}
body .card-item.ant-card .card-row-title {
  font-weight: 600;
  font-size: 10px;
  color: #989CA8;
  letter-spacing: 0;
  text-align: center;
  line-height: 11px;
  margin: 3px 0 15px;
  text-transform: uppercase;
}
body .card-item.ant-card .card-row-item {
  font-weight: 500;
  font-size: 14px;
  color: #0D1B2A;
  letter-spacing: 0;
  text-align: center;
  line-height: 21px;
  margin-bottom: 5px;
}

.tag-manager {
  width: 100%;
}
.tag-manager .card-item.ant-card .card-title {
  font-weight: 700;
  font-size: 16px;
  color: #0053FA;
  letter-spacing: 0;
  text-transform: uppercase;
  text-align: left;
}
.tag-manager-header.tag-manager-row {
  border-bottom: 0;
}
.tag-manager-header.tag-manager-row .value,
.tag-manager-header.tag-manager-row .label,
.tag-manager-header.tag-manager-row .description {
  font-weight: 700;
  font-size: 11px;
  color: #989CA8;
  letter-spacing: 0;
}
.tag-manager-row-add {
  width: 25px;
  height: 25px;
  display: flex;
  justify-content: center;
  align-items: center;
  cursor: pointer;
  align-self: flex-start;
}
.tag-manager-row-add svg {
  width: 15px;
  height: 15px;
}
.tag-manager-row {
  width: 100%;
  background: #FFFFFF;
  border-bottom: 1px solid rgba(0, 116, 249, 0.1);
}
.tag-manager-row.hidden {
  display: none;
}
.tag-manager-row:last-child {
  border-bottom: 0px;
}
.tag-manager-row > * {
  height: 100%;
  min-height: 40px;
  align-items: center;
  display: flex;
}
.tag-manager-row .tag-manager-row-editable .form-item {
  opacity: 0;
  position: absolute;
  width: 100%;
  top: 50%;
  left: 0;
  -webkit-transform: translate(0, -50%);
          transform: translate(0, -50%);
  background: white;
  padding-right: 10px;
}
.tag-manager-row .tag-manager-row-editable .form-item .ant-input {
  padding: 0;
  border: 0;
}
.tag-manager-row .tag-manager-row-editable.visible .tag-manager-row-value {
  display: none;
}
.tag-manager-row .tag-manager-row-editable.visible .form-item {
  opacity: 1;
}
.tag-manager-row.gu-mirror {
  box-shadow: 0 10px 50px 0 rgba(0, 116, 249, 0.1);
  opacity: 1;
}
.tag-manager-row .label,
.tag-manager-row .value,
.tag-manager-row .description {
  font-weight: 600;
  font-size: 11px;
  color: #0d1b2a;
  text-align: left;
  letter-spacing: 0;
  flex: 1 1;
  width: 100%;
  cursor: text;
  position: relative;
}
.tag-manager-row .label .form-item-text .ant-input,
.tag-manager-row .value .form-item-text .ant-input,
.tag-manager-row .description .form-item-text .ant-input {
  font-size: 12px;
}
.tag-manager-row:hover .draggable {
  opacity: 1;
}
.tag-manager-row .drag,
.tag-manager-row .no-drag {
  color: #989CA8;
  width: 50px;
}
.tag-manager-row .drag {
  cursor: move;
}
.tag-manager-row .draggable {
  opacity: 0;
}
.tag-manager-row .draggable svg {
  color: inherit;
  pointer-events: none;
  width: 10px;
  height: 10px;
  vertical-align: middle;
}
.tag-manager-row .remove {
  display: flex;
  justify-content: center;
  align-items: center;
  color: #0d1b2a;
  cursor: pointer;
  width: 50px;
}
.tag-manager-row .remove svg {
  color: inherit;
  pointer-events: none;
  width: 15px;
  height: 15px;
  vertical-align: middle;
}

.balance-card {
  min-width: 250px;
  background: #FFFFFF;
  box-shadow: 0 2px 4px 0 rgba(0, 83, 250, 0.34);
  text-align: center;
  padding: 14px 0;
  margin-bottom: 40px;
}
.balance-card > * {
  padding: 0 14px;
}
.balance-card .footer {
  display: flex;
  align-items: center;
  border-top: 1px solid #B5B9C5;
  padding: 14px 30px 0 14px;
  line-height: 16px;
}
.balance-card .footer > span {
  border-bottom: 1px solid #0053FA;
}
.balance-card .footer .dot3-wrapper {
  color: #0053FA;
  padding: 5px;
  position: absolute;
  right: 30px;
}
.balance-card .footer .dot3-wrapper svg {
  -webkit-transform: rotate(90deg);
          transform: rotate(90deg);
  -webkit-transform-origin: top center;
          transform-origin: top center;
}

